#footer {
  z-index: 1;
  border-top: dashed 1px #dfdfdf;
  padding: 1em 0em 1em 0em;
  margin: 0.5em 1em 0em 1em;
  text-align: center;
  font-size: 1.3em;
  color: #9E9E9E;
  font-family: 'Sriracha', cursive;
  font-weight:400;
}

.app__status {
  display: flex;
  justify-content: space-between;
}

.app__rigth {
  display: flex;
  flex-direction: column;
}

.app__rigth .MuiCardContent-root {
  display: flex !important;
  flex-direction: column;
  flex-grow: 1;
}

.app__graph {
  flex-grow: 1;
}

.app__graphTitle {
  margin-top: 20px;
  margin-bottom: 20px;
}

@media (max-width: 990px){
  .app {
    flex-direction: column;
  }
  
}

.infoBox {
    flex: 1 1;
    cursor: pointer;
}

.infoBox:not(:last-child){
    margin-right: 15px;
}

.infoBox--selected {
    border-top: 10px solid #adff2f;
}

.infoBox__cases {
    color: #cc1034;
    font-weight: 600;
    font-size: 1.75rem;
    margin-bottom: 0.5rem;
}

.infoBox__total {
    color: #6c757d;
    font-weight: 700 !important;
    font-size: 0.8rem !important;
    margin-top: 15px !important;
}
.map {
    height: 500px;
    background-color: white;
    padding: 1rem;
    border-radius: 20px;
    margin-top: 16px;
    box-shadow: 0 0 8px -4px rgba(0, 0, 0, 0.5);
  }
  
  .map .leaflet-container {
    height: 100%;
    border-radius: 12px;
  }
  
  .info-flag {
    width: 100px;
    height: 100px !important;
    border-radius: 5px;
    border: 1px solid lightgray;
  }
  
  .info-name {
    font-size: 20px;
    font-weight: bold;
    color: #555;
  }
  
  .info-container {
    width: 150px;
  }
  
  .info-flag {
    height: 80px;
    width: 100%;
    background-size: cover;
    border-radius: 8px;
  }
  
  .info-confirmed,
  .info-recovered,
  .info-deaths {
    font-size: 16px;
    margin-top: 5px;
  }
.table {
    margin-top: 20px;
    overflow: auto;
    height: 400px;
    color: #6a5d5d;
}

.table table {    
    display: flex;
    flex-direction: column;
    padding-left: 1px;
    padding-right: 1px;
}

.table tr {
    display: flex;
    justify-content: space-between;
}

.table td {
    padding: 0.5rem;
}

.table tr:nth-of-type(odd) {
    background-color: #f3f2f8;
}
